<template>
  <div class="mw-page page" v-nav="{hideNavbar:true}">
    <div class="mw-page-header">
      <i class="left icon ion-ios-arrow-back" @click="$router.go(-1)"></i>
      <span></span>
      <!-- step10(), step20(),step30(),step0() step11()/-->

      <i class="right" v-if="action==='check'&&(compute.approve_step==='step10'||compute.approve_step==='step11')"
         @click="action='edit'"></i>
      <i class="right" v-else-if="action ==='edit'" @click="action='check'"></i>
      <i class="right" v-else></i>
    </div>
    <div class="mw-page-content has-tab">
      <div style="height: 60px">
        <tabs :tab-items="tabs" :tab-index="tabIndex" :on-tab-click="onTabClick"></tabs>
      </div>
      <div style="flex: 1;position: relative">
        <div class="page-content" v-show="tabIndex===0">
          <cst-item label="" v-show="action!='new'" v-model="compute.compute_no" editType="text" :action="action"
                    :canEdit="false"></cst-item>
          <cst-item label="" v-show="action!='new'" v-model="compute.created_at" editType="text" :action="action"
                    :canEdit="false"></cst-item>
          <cst-item label="" v-model="compute.type_name" editType="text" :action="action"
                    :canEdit="false"></cst-item>
          <cst-item label="" v-model="compute.cst_company_name" editType="text" :action="action"
                    :canEdit="false"></cst-item>
          <cst-item label=""
                    v-model="compute.material"
                    editType="text"
                    :action="action"
                    :hasArrow="true"
                    :canEdit="false"
                    @click.native="changeType('material')">
          </cst-item>

          <cst-item label="" editType="number" :hasArrow="true" :arrowText="compute.cst_products.length"
                    :action="action"
                    :canEdit="false"
                    @click.native="toProdList"></cst-item>
        </div>
        <div class="page-content" v-show="tabIndex===1">
          <cst-item label="" :hasArrow="true" v-model="compute.stuff_cost" @click.native="onArrowClick"
                    editType="number" :action="action" :canEdit="false"></cst-item>
          <cst-item label="" :hasArrow="true" v-model="compute.process_cost" @click.native="onArrowClick1"
                    editType="number" :action="action" :canEdit="false"></cst-item>
          <cst-item label="" v-model="compute.design_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.try_mold_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.post_box_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.delivery_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.manage_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.mold_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.profit_cost" editType="number" :action="action"></cst-item>
          <cst-item label="" v-model="compute.mold_up" editType="number" :action="action"></cst-item>
        </div>
      </div>
    </div>
    <mw-prompt :show="showApprove"
               :okFunction="approveProcess"
               v-on:hidePop="showApprove = false"
               title=""
               placeholder=""
               v-model="approve.suggestion"
               okText=""
               cancelText=""
               :cancelFunction="approveProcess"
               promptType="textarea"
    ></mw-prompt>
    <sidebar-check :showFilter="showFilter" dicType="material" :chosen="choose" v-on:hideMask="showFilter = false"></sidebar-check>
    <!--action=check step20step30-->
    <div class="mw-page-footer"
         v-if="action === 'check'&&(compute.approve_step!=='step20'&&compute.approve_step!=='step30')">
      <div class="btns">
        <div class="btn" v-if="(compute.approve_step==='step11'||compute.approve_step==='step10')" @click="submit">
          
        </div>
        <div class="btn" v-show="compute.approve_step==='step11'||compute.approve_step==='step0'" @click="checkApprove">
          
        </div>
      </div>
    </div>
    <div class="mw-page-footer" v-else-if="action === 'new'||action === 'edit'||action === 'associate'">
      <div class="btns">
        <div class="btn" @click="saveData">
          
        </div>
      </div>
    </div>
    <div class="mw-page-footer" v-else-if="action==='approve'">
      <div class="btns">
        <div class="btn" @click="submit">
          
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import  cstItem  from '@/components/cstItem'
  import {detail, save, submitApprove, associate} from '@/api/customerOrder/compute'
  import sess from '@/utils/sess'
  import SidebarCheck from "../../../../components/sidebarCheck";
  import MwPrompt from "../../../../components/mwPrompt";
  import { mapGetters } from 'vuex'
  export default{
    name: 'computeDetail',
    components: {
      MwPrompt,
      SidebarCheck,
      cstItem
    },
    data(){
      return {
        action: '',
        tabs: ['', ''],
        tabIndex: 0,
        showFilter: false,
        compute: {
          compute_id: '',
          compute_no: '',
          approval_flag: '',
          created_date: '',
          cst_company_id: '',
          cst_company_name: '',
          cst_mold_id: '',
          cst_mold_type: '',
          type: '',
          type_name: '',
          delivery_cost: '',
          design_cost: '',
          cst_products: [],
          manage_cost: '',
          material: '',
          mold_cost: '',
          mold_up: '',
          post_box_cost: '',
          process_cost: '',
          profit_cost: '',
          stuff_cost: '',
          try_mold_cost: '',
          valid_date: ''
        },
        materialList: [],
        compute_id: '',
        showApprove:false,  //
        approve:{
          suggestion:'',
          approve_step:''
        }
      }
    },
    computed:{
      ...mapGetters(['userinfo'])
    },
    mounted(){
      console.log('mounted')
      this.action = this.$route.params.action
      if (this.action === 'new') {
        //
        const obj = this.$route.query
        console.log(obj)
        this.compute.cst_company_id = obj.cst_company.cst_company_id
        this.compute.cst_company_name = obj.cst_company.cst_company_name
        this.compute.type_name = obj.type.name
        this.compute.type = obj.type.code
      } else if (this.action === 'check' || this.action === 'approve') {
        this.compute_id = this.$route.params.compute_id
        this.compute.compute_id = this.$route.params.compute_id
        this.getData();
      } else if (this.action === 'associate') {
        const reqData = {
          compute_id: this.$route.params.compute_id
        }
        associate(reqData).then(res => {
          console.log(res)
          this.action = 'edit'
          this.compute_id = this.compute.compute_id = res.compute_info.compute_id
          this.compute = res.compute_info
        }).catch(error => {
          $toast.show(error.description)
        })
      }
    },
    activated(){
      console.log('activated')
      const prods = sess.get('prods');
      if (prods && prods != '') {
        this.compute.cst_products = JSON.parse(prods)
        sess.remove('prods')
        return
      }
      if(this.compute.compute_id){
        this.getData()
      }
    },
    methods: {
      onArrowClick(){
        $router.forward('/customerOrder/compute/cst_part_list/' + this.action + '/' + this.compute_id)
      },
      onArrowClick1(){
        $router.forward('/customerOrder/compute/cst_process_list/' + this.action + '/' + this.compute_id)
      },
      changeType(type){
        if (this.action === 'check'||this.action === 'approve') {
          return
        }
        this.showFilter = true
      },
      choose(item){
        this.compute.material = item.raw_nature
      },
      onMenuClick(){
        if (this.action === 'check') {
          this.action = 'edit'
        } else {
          this.action = 'check'
        }
      },
      onTabClick(index){
        console.log('onTabClick')
        if (!this.compute_id) {
          $toast.show('')
          return
        }
        this.tabIndex = index
      },
      getData(){
        const that = this;
        const requestData = {
          compute_id: that.compute_id
        }
        detail(requestData).then(res => {
          that.compute = res
        }).catch(error => {
          $toast.show(error.description)
        })
      },
      saveData(){
        if (this.compute.cst_products.length===0) {
          $toast.show('')
          return
        }
        const requestData = {
          compute_id: this.compute_id,
          compute_info: this.compute
        }
        save(requestData).then(res => {
          this.action = this.action === 'new' ? 'edit' : 'check'
          this.compute_id = this.compute.compute_id = res.compute_info.compute_id
          this.compute = res.compute_info
          $toast.show('')
        }).catch(error => {
          $toast.show(error.description)
        })
      },
      submit(){
        if (this.action === 'edit') {
          $toast.show('')
          return
        }
        if (this.action === 'approve') {
          this.showApprove = true
//          this.$router.forward('/approve/approve/' + this.compute_id + '/1/' + this.compute.approve_step)
        } else {
          const reqData = {
            bill_id: this.compute_id,
            bill_type: '1',
            approve_flag: '1',
            approve_step: this.compute.approve_step
          }
          submitApprove(reqData).then(res => {
            $toast.show('')
            sess.set('approve', true)
            this.$router.go(-1)
          }).catch(error => {
            $toast.show(error.description)
            console.log(error.description)
          })
        }
      },
      checkApprove(){
        //
        this.$router.forward('/approve/approveSuggests/' + this.compute.compute_id + '/1')
      },
      toProdList(){
        let m_action = this.action
        if(this.compute.approve_step==='step0'){
          m_action = 'readOnly'
        }
        $router.forward({
          path:'/customerOrder/compute/cst_prod_list/'+m_action,
          query:{
            cst_prods:JSON.stringify(this.compute.cst_products),
          }
        })
      },
      approveProcess(flag){
        console.log('approveProcess')
        flag = flag === 'ok' ? 1 : 2
        const reqData = {
          approve_sugg:this.approve.suggestion,
          bill_id: this.compute_id,
          bill_type: 1,
          approve_step: this.compute.approve_step,
          approve_man_id:this.userinfo.uid,
          approve_man_name:this.userinfo.username,
          approve_flag:flag
        }
        submitApprove(reqData).then(res=>{
//          $toast.show('')
          history.go(-1)
        }).catch(error=>{
          $toast.show(error.description)
        })
      }
    },
  }
</script>
<style lang="scss" scoped>

  .page-content{
    height: 100%;
  }
</style>



